.content {
  max-width: 100%;
  display: flex;
  gap: 8%;

  &__image-wrapper {
    & svg {
      max-width: 100%;
    }
  }

  &__title {
    margin: 0;
    font-size: 48px;
    font-weight: 700;
    line-height: 110%;
    font-family: "RobotoSlab", "Times New Roman", serif;
  }

  &__form-wrapper {
    width: 40%;
  }
}
