@font-face {
  font-style: normal;
  font-family: "RobotoSlab";
  font-display: swap;
  src: url("/fonts/RobotoSlab-VariableFont_wght.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-family: "Inter";
  font-display: swap;
  src: url("/fonts/Inter-VariableFont_wght.woff2") format("woff2");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Inter", "Arial", sans-serif;
  color: #343434;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #fdfeff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.page-container {
  padding: 0 8% 0;
  background-color: #fdfeff;
}

.main-header {
  display: flex;
  padding: 60px 0 0;
}

.main-container {
  padding: 112px 0;
}

.content {
  max-width: 100%;
  display: flex;
  gap: 8%;
}
.content__image-wrapper svg {
  max-width: 100%;
}
.content__title {
  margin: 0;
  font-size: 48px;
  font-weight: 700;
  line-height: 110%;
  font-family: "RobotoSlab", "Times New Roman", serif;
}
.content__form-wrapper {
  width: 40%;
}

.user-form {
  margin: 60px 0 0;
}
.user-form__label {
  display: block;
  margin: 0;
  padding: 16px 10px;
  position: relative;
  border: 1px solid #eaeaea;
  border-radius: 10px;
}
.user-form__label:focus-within {
  border-color: #3ca9f0;
}
.user-form__label:focus-within .user-form__label-text {
  color: #3ca9f0;
}
.user-form__label--lesson {
  flex-grow: 1;
  display: flex;
  align-items: center;
  gap: 15px;
}
.user-form__label--class {
  width: 15%;
  min-width: 120px;
  display: flex;
  align-items: center;
}
.user-form__label--error {
  border-color: #ff395d;
}
.user-form__label--error .user-form__label-text {
  color: #ff395d;
}
.user-form__label-text {
  display: block;
  position: absolute;
  top: -12px;
  left: 16px;
  background-color: #fdfeff;
  padding: 0 4px;
  font-size: 16px;
  font-weight: 400;
  line-height: normal;
}
.user-form__input {
  width: 100%;
  border: 0;
  font-family: "Inter", "Arial", sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: normal;
}
.user-form__input::placeholder {
  font-family: "Inter", "Arial", sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: normal;
}
.user-form__input:focus-visible {
  outline: 0;
}
.user-form__input-icon {
  display: block;
}
.user-form__input-icon--hidden {
  display: none;
}
.user-form__info-wrapper {
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
}
.user-form__select-list {
  margin: 0;
  padding: 0;
  position: absolute;
  top: 32px;
  left: -1px;
  list-style-type: none;
  border-radius: 12px;
  border: 1.25px solid #eaeaea;
  background: #ffffff;
  box-shadow: 0px 4px 12px 0px rgba(0, 0, 0, 0.12);
}
.user-form__select-list--hidden {
  display: none;
}
.user-form__select-item {
  min-width: 120px;
  padding: 10px 16px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.user-form__select-item--checked .user-form__select-text {
  font-weight: 600;
}
.user-form__select-item--checked .user-form__select-icon {
  display: block;
}
.user-form__select-text {
  font-size: 18px;
  font-weight: 400;
  line-height: normal;
  font-size: 18px;
  font-weight: 400;
  line-height: normal;
}
.user-form__select-icon {
  width: 24px;
  height: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
  display: none;
}
.user-form__select-icon svg {
  height: 100%;
}
.user-form__textarea {
  margin: 0;
  padding: 0;
  border: 0;
  width: 100%;
  height: 3em;
  resize: none;
  font-family: "Inter", "Arial", sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: normal;
}
.user-form__textarea::placeholder {
  font-family: "Inter", "Arial", sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: normal;
}
.user-form__textarea:focus-visible {
  outline: 0;
}
.user-form__text-wrapper {
  margin: 48px 0 0;
}
.user-form__tag-wrapper {
  margin-top: 20px;
}
.user-form__tag-list {
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  list-style-type: none;
}
.user-form__tag-list--hidden {
  list-style-type: none;
}
.user-form__tag-item {
  padding: 10px 16px;
  background-color: #eeeff0;
  border-radius: 100px;
  font-size: 16px;
  font-weight: 500;
  line-height: normal;
}
.user-form__tag-item--hidden {
  display: none;
}
.user-form__textarea-message {
  color: #ff395d;
  font-family: "Inter", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
}
.user-form__textarea-message--hidden {
  display: none;
}
.user-form__text-info {
  padding: 0 17px;
  display: flex;
  justify-content: space-between;
}
.user-form__textarea-counter {
  margin-left: auto;
  font-family: "Inter", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
}
.user-form__textarea-counter--error {
  color: #ff395d;
}
.user-form__more-button {
  margin-top: 20px;
  border: 0;
  background-color: transparent;
  color: #3ca9f0;
  font-family: "Inter", "Arial", sans-serif;
  font-size: 18px;
  font-weight: 500;
  line-height: normal;
}
.user-form__control-wrapper {
  margin: 32px 0 0;
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
}
.user-form__control-button {
  padding: 18px 52px;
  border: 1px solid #3ca9f0;
  border-radius: 12px;
  background-color: transparent;
  color: #3ca9f0;
  font-family: "Inter", "Arial", sans-serif;
  font-size: 18px;
  font-weight: 500;
  line-height: normal;
}
.user-form__control-button--next {
  border-color: #3ca9f0;
  background-color: #3ca9f0;
  color: #ffffff;
}
.user-form__control-button:disabled {
  border-color: #eeeff0;
  background-color: #eeeff0;
  color: #9a9a9a;
}