.user-form {
  margin: 60px 0 0;

  &__label {
    display: block;
    margin: 0;
    padding: 16px 10px;
    position: relative;
    border: 1px solid #eaeaea;
    border-radius: 10px;

    &:focus-within {
      border-color: #3ca9f0;

      & .user-form__label-text {
        color: #3ca9f0;
      }
    }

    &--lesson {
      flex-grow: 1;
      display: flex;
      align-items: center;
      gap: 15px;
    }

    &--class {
      width: 15%;
      min-width: 120px;
      display: flex;
      align-items: center;
    }

    &--error {
      border-color: #ff395d;

      & .user-form__label-text {
        color: #ff395d;
      }
    }
  }

  &__label-text {
    display: block;
    position: absolute;
    top: -12px;
    left: 16px;
    background-color: #fdfeff;
    padding: 0 4px;
    font-size: 16px;
    font-weight: 400;
    line-height: normal;
  }

  &__input {
    width: 100%;
    border: 0;
    font-family: "Inter", "Arial", sans-serif;
    font-size: 18px;
    font-weight: 400;
    line-height: normal;

    &::placeholder {
      font-family: "Inter", "Arial", sans-serif;
      font-size: 18px;
      font-weight: 400;
      line-height: normal;
    }

    &:focus-visible {
      outline: 0;
    }
  }

  &__input-icon {
    display: block;

    &--hidden {
      display: none;
    }
  }

  &__info-wrapper {
    display: flex;
    flex-wrap: wrap;
    gap: 16px;
  }

  &__select-list {
    margin: 0;
    padding: 0;
    position: absolute;
    top: 32px;
    left: -1px;
    list-style-type: none;
    border-radius: 12px;
    border: 1.25px solid #eaeaea;
    background: #ffffff;
    box-shadow: 0px 4px 12px 0px rgba(0, 0, 0, 0.12);

    &--hidden {
      display: none;
    }
  }

  &__select-item {
    min-width: 120px;
    padding: 10px 16px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    &--checked {
      & .user-form__select-text {
        font-weight: 600;
      }

      & .user-form__select-icon {
        display: block;
      }
    }
  }

  &__select-text {
    font-size: 18px;
    font-weight: 400;
    line-height: normal;
    font-size: 18px;
    font-weight: 400;
    line-height: normal;
  }

  &__select-icon {
    width: 24px;
    height: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
    display: none;

    & svg {
      height: 100%;
    }
  }

  &__textarea {
    margin: 0;
    padding: 0;
    border: 0;
    width: 100%;
    height: 3em;
    resize: none;
    font-family: "Inter", "Arial", sans-serif;
    font-size: 18px;
    font-weight: 400;
    line-height: normal;

    &::placeholder {
      font-family: "Inter", "Arial", sans-serif;
      font-size: 18px;
      font-weight: 400;
      line-height: normal;
    }

    &:focus-visible {
      outline: 0;
    }
  }

  &__text-wrapper {
    margin: 48px 0 0;
  }

  &__tag-wrapper {
    margin-top: 20px;
  }

  &__tag-list {
    margin: 0;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
    list-style-type: none;

    &--hidden {
      list-style-type: none;
    }
  }

  &__tag-item {
    padding: 10px 16px;
    background-color: #eeeff0;
    border-radius: 100px;
    font-size: 16px;
    font-weight: 500;
    line-height: normal;

    &--hidden {
      display: none;
    }
  }

  &__textarea-message {
    color: #ff395d;
    font-family: "Inter", "Arial", sans-serif;
    font-size: 14px;
    font-weight: 400;
    line-height: 21px;

    &--hidden {
      display: none;
    }
  }

  &__text-info {
    padding: 0 17px;
    display: flex;
    justify-content: space-between;
  }

  &__textarea-counter {
    margin-left: auto;
    font-family: "Inter", "Arial", sans-serif;
    font-size: 14px;
    font-weight: 400;
    line-height: 21px;

    &--error {
      color: #ff395d;
    }
  }

  &__more-button {
    margin-top: 20px;
    border: 0;
    background-color: transparent;
    color: #3ca9f0;
    font-family: "Inter", "Arial", sans-serif;
    font-size: 18px;
    font-weight: 500;
    line-height: normal;
  }

  &__control-wrapper {
    margin: 32px 0 0;
    display: flex;
    flex-wrap: wrap;
    gap: 16px;
  }

  &__control-button {
    padding: 18px 52px;
    border: 1px solid #3ca9f0;
    border-radius: 12px;
    background-color: transparent;
    color: #3ca9f0;
    font-family: "Inter", "Arial", sans-serif;
    font-size: 18px;
    font-weight: 500;
    line-height: normal;

    &--next {
      border-color: #3ca9f0;
      background-color: #3ca9f0;
      color: #ffffff;
    }

    &:disabled {
      border-color: #eeeff0;
      background-color: #eeeff0;
      color: #9a9a9a;
    }
  }
}
